.modal-dialog {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 99999;
    width: 100%;
    height: 100%;
    background: #0000007a;

    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    -o-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
}
.modal-open {
    overflow: hidden;
}
@keyframes fadeIn_v2 {
    0% {transform: translate(0, -50%);}
    50% { transform: translate(0, -25%); }
    100% { transform: translate(0, 0%); }
}
@keyframes fadeIn {
    0% { opacity: 0; }
    0.1% { opacity: 0; }
    100% { opacity: 1; }
}

.modal-opening {
    opacity: 1;
    display: block;
    animation: fadeIn 0.5s linear forwards;
    .modal-content {
        animation: fadeIn_v2 0.3s linear forwards;
    }
}

.modal-close {
    position: absolute;
    right: 60px;
    top: 60px;
    background: transparent;
    border: none;
    cursor: pointer;

    span {
        font-size: 32px;
        color: #B5C6D7;
    }
}


