.swiper {
    .swiper-button-next, .swiper-button-prev {
        background: #0183FF;
        border-radius: 50%;
        width: 56px;
        height: 56px;
        top: 65%;
        &:hover {
            background: #4399eb;
        }
        &:after {
            content: "";
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            width: 24px;
            height: 24px;
        }
    }
    .swiper-button-next {
        &:after {
            background-image: url(../img/icons/swiper-arrow-right.svg);
        }
    }
    .swiper-button-prev {
        &:after {
            background-image: url(../img/icons/swiper-arrow-left.svg);
        }
    }
}

.accordion {
    width: 100%;
    .accordion-item {
        margin-bottom: 12px;
        width: 100%;
        cursor: pointer;
        .accordion-item-content {
            overflow: hidden;
            padding: 0 28px;
            border-top: none;
            max-height: 0;
            transition: 0.5s;
            width: 100%;

            p {
                padding: 30px 0 20px;
                color: #232323;
            }
        }
        .accordion-item-header {
            position: relative;
            background: #F4F7FB;
            border-radius: 15px;
            height: 60px;
            display: flex;
            align-items: center;
            padding-left: 35px;
            font-weight: 500;
            font-size: 20px;
            line-height: 25px;
            letter-spacing: 0.0015em;
            color: #232323;
            transition: 0.3s;
            padding-right: 60px;
            &:hover {
                background: #ECF3FD;
            }
            &:after {
                content: "";
                display: block;
                background-image: url(../img/icons/accodion-arrow-dropdown.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                width: 12px;
                height: 12px;
                position: absolute;
                right: 30px;
                top: 40%;
                transition: 0.2s;
            }
        }
    }
    .accordion-item.show {
        .accordion-item-header {
            &:after {
                transform: rotate(180deg);
            }
        }
        .accordion-item-content {
            max-height: 1000px;
            transition: all 2s;
        }
    }
}

