    .diagram-line {
        height: 1px;
        background: #BABABA;
        width: 830px;
        display: flex;
        justify-content: space-between;
        position: relative;
        .diagram-point {
            width: 8px;
            height: 8px;
            border: 1px solid #BABABA;
            border-radius: 50%;
            background: #fff;
            margin-top: -3px;
            position: relative;
            &:after {
                font-weight: 600;
                font-size: 14px;
                line-height: 18px;
                letter-spacing: 0.0015em;
                color: #DADADA;
                position: absolute;
                top: 15px;
                left: -15px;
            }
            &:nth-child(1) {
                &:after {
                    content: "0";
                    left: 0;
                }
            }
            &:nth-child(2) {
                &:after {
                    content: "$500k";
                    left: -20px;
                }
            }
            &:nth-child(3) {
                &:after {
                    content: "$1m";
                }
            }
            &:nth-child(4) {
                &:after {
                    content: "$1.5m";
                }
            }
            &:nth-child(5) {
                &:after {
                    content: "$2m";
                }
            }
        }
    }
    .diagram-total {
        position: absolute;
        right: -30px;
        p {
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            letter-spacing: 0.0015em;
            color: #232323;
